import {connect} from "react-redux";
import React, {Component} from "react";
import {OraButton} from "eshop/components/OraCommonComponents";
import OraModal from "eshop/modules/core/components/ui/Modal";
import {getRating,getMessage,getLogin,getShowRatingErrorModal} from "../modules/simfree/selectors";
import {setRating,setMessage,setLogin,setShowRatingErrorModal} from "../modules/simfree/actions/app";

class OraRatingModalComponent extends Component{

    constructor(props) {
        super(props);
    }

    popupMessage(){
        if(!this.props.rating && !this.props.message && !this.props.login){
            return this.props.descriptions.allFieldsEmptyMessage
        } else if(!this.props.rating && this.props.message && this.props.login){
            return this.props.descriptions.ratingMessage
        } else if(!this.props.login && this.props.rating && this.props.message){
            return this.props.descriptions.emptyLoginMessage
        } else if(!this.props.message && this.props.rating && this.props.login){
            return this.props.descriptions.emptyOpinionMessage
        } else if(!this.props.rating && !this.props.login && this.props.message){
            return this.props.descriptions.emptyRatingAndLoginMessage
        } else if(!this.props.rating && !this.props.message && this.props.login){
            return this.props.descriptions.emptyRatingAndOpinionMessage
        } else if(!this.props.login && !this.props.message && this.props.rating){
            return this.props.descriptions.emptyLoginAndOpinionMessage
        }
    }

    render(){
        return (
            <OraModal id={"rating-message-error"} showClose={true} open={this.props.showRatingErrorModal}
                      onClose={() => this.props.setShowRatingErrorModal(false)} size="narrow" clickClose={false}>
                <div className="u-padding-all-m u-small-padding-s">
                    <div className="l-row u-letter-spacing-xs">
                        <div className="l-col l-col-12">
                            <h4>
                                {this.popupMessage()}
                            </h4>
                        </div>
                        <div
                            className="l-col l-col-small-12 l-col-medium-4 l-col-4 medium-offset-by-8 large-offset-by-8 u-text-right">
                            <OraButton onClick={() => this.props.setShowRatingErrorModal(false)} className="u-w-100 ">
                                OK
                            </OraButton>
                        </div>
                    </div>
                </div>
            </OraModal>
        )
    }
}

let mapStateToProps = state => ({
    rating: getRating(state),
    message: getMessage(state),
    login: getLogin(state),
    showRatingErrorModal: getShowRatingErrorModal(state)
});
let mapDispatchToProps = dispatch=> ({
    setRating: (rating) => dispatch(setRating(rating)),
    setLogin: (login) => dispatch(setLogin(login)),
    setMessage: (message) => dispatch(setMessage(message)),
    setShowRatingErrorModal: (showRatingErrorModal) => dispatch(setShowRatingErrorModal(showRatingErrorModal))
});

export default connect(mapStateToProps, mapDispatchToProps)(OraRatingModalComponent)
